@import url(https://fonts.googleapis.com/css2?family=Carter+One&family=Days+One&family=Epilogue:wght@100&family=Merriweather+Sans:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.login-form {
  display: flex;
  flex-direction: column;
  margin: auto;
  width: 80%;
}

.login-form > * {
  border: 1px solid gray;
  border-radius: 4px;
  height: 40px;
}

.login-form input {
  text-indent: 10px;
}

.login-form button {
  background-color: #3c8fdc;
  color: white;
  margin-top: 10px;
}

.login-form button:disabled {
  background-color: #6491bb;
  color: rgb(199, 199, 199);
}

.login-page {
  border: 1px solid gray;
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  left: 50%;
  margin: -220px -210px;
  padding: 80px 10px;
  position: fixed;
  grid-row-gap: 60px;
  row-gap: 60px;
  text-align: center;
  top: 50%;
  width: 400px;
}

.not-found-text {
  left: 50%;
  margin: -20px -205px;
  position: fixed;
  top: 50%;
}

table {
  border-collapse: collapse;
  width: 100%;
}

thead tr th {
  background-color: #31393e;
  color: white;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: small;
  padding: 16px 20px;
}

tbody tr {
  color: #272a33;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: small;
  height: 60px;
  text-align: center;
}

/* botões da tabela */
.expense-row-btn {
  border: none;
  border-radius: 4px;
  box-shadow: 0 1px 2px rgb(78, 78, 78);
  color: rgb(39, 38, 38);
  cursor: pointer;
  font-weight: 600;
  padding: 4px;
}

.edit {
  margin-right: 6px;
}

.delete {
  background-color: rgba(250, 56, 56, 0.836);
}


header {
  align-items: center;
  display: flex;
  justify-content: space-between;
  padding: 0 40px;
}

h2 {
  font-family: Days One , sans-serif;
  font-weight: 100;
}

.user-data {
  grid-gap: 20px;
  gap: 20px;
}

header div {
  align-items: center;
  display: flex;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: small;
  font-weight: 600;
  grid-gap: 5px;
  gap: 5px;
}

/* formulário de despesa */
.expense-form {
  background-color: #272a33;
  color: white;
  display: flex;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: smaller;
  justify-content: center;
  padding: 20px;
}

.expense-form label {
  align-items: center;
  display: flex;
  grid-gap: 5px;
  gap: 5px;
  margin-right: 15px;
}

.expense-form label input {
  text-indent: 3px;
}

.expense-form label > input , select {
  background-color: white;
  border: none;
  border-radius: 4px;
  color: #686a6d;
  height: 60%;
}

.expense-form button {
  align-self: center;
  background-color: #3c8fdc;
  border: none;
  border-radius: 4px;
  color: white;
  padding: 6px;
}

.expense-form button:disabled {
  background-color: #6491bb;
  color: rgb(199, 199, 199);
}

/* formulário de despesa no modo 'editar gasto' */
.editing-true {
  background-color: #00ae69;
  color: rgb(15, 15, 15);
}


